.spinnyDubyk{
	position: absolute;
	visibility: hidden;
	/* animation: transition 2s 1; */
}

@keyframes transition {
	0%{
		visibility: visible;
		transform: scale(0.01) rotate(360deg);
		opacity: 0;
	}
	10%{
		opacity: 1;
	}
	50%{
		transform: scale(1) rotate(0);
	}
	90%{
		opacity: 1;
	}
	100%{
		transform: scale(0.01) rotate(-360deg);
		opacity: 0;
	}
}

hr{
	border-color: lightgrey;
	background-color: lightgrey;
	margin-top: -15px;
	margin-bottom: 15px;
}

.above{
	z-index: 2;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	text-align: center;
	padding-right: 25px;
	padding-left: 25px;
}
.dubyk{
	position: absolute;
	bottom: -50px;
	left: -60px;
	transform: rotate( 30deg);
}
.sax{
	position: absolute;
	bottom: -35px;
	left: 160px;
	transform: rotate(30deg);
	animation: saxy 1s infinite;
}

@keyframes saxy {
	0%{
		transform: rotate(30deg);
	}
	50%{
		transform: rotate(0deg);
	}
	100%{
		transform: rotate(30deg);
	}
}

@media (max-width: 925px) {
	.dubyk{
		position: static;
		transform: none;
		margin-top: 50px;
		margin-right: 30px;
	}

	.sax{
		bottom: 25px;
		left: 50%;
	}

	.main{
		padding-bottom: 0;
		padding-top: 75px;
	}


}

